<template>
  <div>
    <h1>{{ part.title }}</h1>
    <p>{{ part.description }}</p>
    <img :src="part.src" :alt="part.description" />
  </div>
</template>

<script>
import getPartsMixin from "./get-parts-mixin";
export default {
  name: "PartInfo",
  props: ["partType", "id"],
  mixins: [getPartsMixin],
  computed: {
    part() {
      const { partType, id } = this;
      return this.parts[partType].find((part) => part.id === +id);
    },
  },
};
</script>

